import { Injectable } from '@angular/core';
import {Subject} from 'rxjs';
import {Video} from '../classes/Video';
import firebase from 'firebase/app';
import DataSnapshot = firebase.database.DataSnapshot;

@Injectable({
  providedIn: 'root'
})
export class VideoService {

  ref: string = "videos";
  videos: Array<Video> = [];
  videoSubject = new Subject<Array<Video>>();

  constructor() {}

  emit() {
    this.videoSubject.next(this.videos);
  }

  getAll() {
    return new Promise(
      (resolve, reject) => {
        firebase.database().ref('/' + this.ref).on('value', (data: DataSnapshot) => {
          this.videos = data.val() ? data.val() : [];
          this.emit();
          resolve(true);
        }, (error) => {
          console.error(error)
          reject(error);
        }
        );
      }
    );
  }

  // get(id: number) {
  //   return new Promise(
  //     (resolve, reject) => {
  //       firebase.database().ref('/' + this.ref + '/' + id).once('value').then(
  //         (data: DataSnapshot) => {
  //           resolve(data.val() as Video);
  //         }, (error) => {
  //           reject(error);
  //         }
  //       );
  //     }
  //   );
  // }
}
